.location {
    background: #000;
    #sub_main {
        .txtFirst {
            width: 340px;
        }
    }
    .inner {
        padding-bottom: 0;
        .box {
            display: flex;
            justify-content: space-between;
            min-height: 200px;
            margin-bottom: 40px;
            .d_box {
                width: 280px;
                text-align: center;
                border: 1px solid #999;
                padding: 40px 0;
                cursor: pointer;
                .icons {
                    font-size: 30px;
                    color: #efefef;
                }
                h2 {
                    font: bold 18px/1 'Lora';
                    color: #efefef;
                    margin: 15px 0;
                }
                p {
                    font: 11px/1.3 'arial';
                    color: #999;
                }
            }
        }
        .mapbox {
            height: 500px;
            position: relative;
            article {
                height: 490px;
                display: flex;
                .contact {
                    width: 380px;
                    background: #eaeaea;
                    padding: 20px 40px;
                    h1 {
                        font: bold 30px/1 'Lora';
                        color: #000;
                        margin: 10px 0 20px;
                    }
                    span {
                        color: #000;
                        font: 13px/1 'Lora';
                    }
                    input[type=text] {
                        background: #eaeaea;
                        border: none;
                        border-bottom: 2px solid #555;
                        margin: 5px 0 20px;
                        padding-left: 10px;
                        font: 11px/1 'arial';
                        color: #000;
                    }
                    textarea {
                        background: #eaeaea;
                        border: none;
                        border-bottom: 2px solid #555;
                        margin-top: 10px;
                        padding: 10px;
                        height: 100px;
                        color: #000;
                        font: 11px/1 'arial';
                    }
                    input[type=checkbox] {
                        color: #eaeaea;
                        position: absolute;
                        width: 13px;
                        display: flex;
                        margin-top: 10px;
                    }
                    p {
                        margin: 15px 25px 0 ;
                        font: 10px/1.3 'arial';
                        color: #555;
                        letter-spacing: 0.5px;
                    }
                    button {
                        margin-top: 20px;
                        width: 100%;
                        height: 30px;
                        background: #000;
                        border: none;
                        font: normal 10px/1 'arial';
                        color: #fff;
                        cursor: pointer;
                        &:hover {
                            background: #c2b096;
                            color: #000;
                        }
                    }
                }
                #map {
                    width: 800px;
                    height: 450px;
                    filter: grayscale(1);
                }
            }
            button {
                width: 150px;
                height: 30px;
                background: #fff;
            }
            .branch {
                display: inline-block;
                button {
                    position: relative;
                    width: calc(803px / 3);
                    height: 40px;
                    top: -40px;
                    right: -380px;
                    font: 10px/1 'arial';
                    color: #666;
                    transition: 1s;
                    background: #eaeaea;
                    margin-left: -1px;
                    &:nth-of-type(3) {
                        margin-right: 0;
                    }
                    &:hover {
                        border-bottom: 10px solid #c2b096;
                    }
                }
            }
        }
        .box_bottom {
            margin-top: 50px;
            &::after {
                content: '';
                display: block;
                clear: both;
            }
            h2 {
                font: normal 35px/1 'arial';
                color: #fff;
                width: 350px;
            }
            p {
                width: 500px;
                margin: 20px 0;
                color: #999;
            }
            article {
                float: left;
                width: 280px;
                margin: 30px 20px 0 0;
                text-align: center;
                &:nth-of-type(4) {
                    margin-right: 0;
                }
                .bottom_icons {
                    font-size: 45px;
                    color: #efefef;
                }
                p {
                    font: 10px/1.5 'arial';
                    color: #999;
                    width: 280px;
                }
            }
        }
    }
}

@media screen and (max-width: $tablet) {
    .location {
        overflow: visible;
        .inner {
            width: 100%;
            height: 100%;
            padding: 0 10px ;
            .mapbox {
                height: 100%;
                article {
                    height: 100%;
                    .contact {
                        width: 33.3%;
                        padding: 10px;
                        h1 {
                            font: bold 20px/1 'Lora';
                        }
                        p {
                            margin: 10px 0 0 20px;
                            font: 10px/1 'arial';
                            color: #555;
                        }
                    }
                    #map {
                        width: 66.6%;
                    }
                }
                .branch {
                    display: inline-block;
                    width: 100%;
                    button {
                        position: relative;
                        margin-top: 20px;
                        margin-right: 10px;
                        width: calc(95% / 3);
                        top: 0;
                        right: 0;
                        margin-left: 0px;
                        &:nth-of-type(3) {
                            margin-right: 0;
                        }
                    }
                }
            }
            .box_bottom {
                width: 100%;
                h2 {
                    font: normal 30px/1 'arial';
                    width: 100%;
                }
                p {
                    width: 100%;
                }
                article {
                    float: left;
                    width: calc(100% /2);
                    margin: 30px 0px;
                    &:nth-of-type(2n) {
                        margin-right: 0;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $mobile) {
    .location {
        #sub_main {
            .txtFirst {
                width: 250px;
            }
        }
        .inner {        
            .box {
                min-height: 100px;
                .d_box {
                    width: calc(100% /4);
                    h2 {
                        font: bold 10px/1 'Lora';
                    }
                    p {
                        display: none;
                    }
                }
            }
            .mapbox {
                article {
                    .contact {
                        p {
                            display: none;
                        }
                        button {
                            margin-top: 50px;
                        }
                    }
                }
                .branch {
                    button {
                        width: calc(90% / 3);
                    }
                }
            }
            .box_bottom {
                width: 100%;
                min-height: 200px;
                p {
                    display: none;
                }
                article {
                    margin: 30px 0px;
                }
            }
        }
    }
}